* {
  margin: 0;
  padding: 0;
}

html {
  --primary-blue-color: #00acdc;
  --header-height: 60px;
}

.roboto-medium {
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  height: var(--header-height);
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__container {
  height: var(--header-height);
  justify-content: flex-end;
  align-items: stretch;
  font-size: 12px;
  display: flex;
}

.nav__img {
  text-align: center;
  width: 40px;
  height: 40px;
}

a.nav__link.is-active {
  color: var(--primary-blue-color);
  position: relative;
}

a.nav__link.is-active:after {
  content: " ";
  background-color: var(--primary-blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
}

.nav__link:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--primary-blue-color);
}
